'use client';

import { useState } from 'react';
import TripForm from './components/TripForm';
import ItineraryDisplay from './components/ItineraryDisplay';
import type { TripFormData } from './types/trip';
import { Plane } from 'lucide-react';

export default function Home() {
    const [isLoading, setIsLoading] = useState(false);
    const [itinerary, setItinerary] = useState<string | null>(null);
    const [error, setError] = useState<string | null>(null);

    const handleSubmit = async (formData: TripFormData) => {
        setIsLoading(true);
        setError(null);
        setItinerary(null);

        try {
            // Call the API route to generate itinerary using OpenAI
            const response = await fetch('/api/itinerary', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    destination: formData.destination,
                    days: formData.days,
                    budget: formData.budget,
                    style: formData.travel_style,
                    hotel: formData.starting_location,
                }),
            });

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.error || 'Failed to generate itinerary');
            }

            const data = await response.json();
            setItinerary(data.itinerary);
        } catch (err) {
            console.error('Error creating itinerary:', err);
            setError(err instanceof Error ? err.message : 'Failed to generate itinerary. Please try again.');
        } finally {
            setIsLoading(false);
        }
    };

    const handleReset = () => {
        setItinerary(null);
        setError(null);
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-purple-50 py-12 px-4">
            <div className="max-w-7xl mx-auto">
                {!itinerary ? (
                    <div className="flex flex-col items-center justify-center min-h-[80vh]">
                        <div className="mb-8 flex items-center gap-3">
                            <div className="p-3 bg-blue-600 rounded-xl shadow-lg">
                                <Plane className="w-8 h-8 text-white" />
                            </div>
                            <h1 className="text-5xl font-bold text-gray-800">TripGenius AI</h1>
                        </div>
                        <p className="text-gray-600 text-lg mb-8 text-center max-w-2xl">
                            Powered by OpenAI ‚Ä¢ Get personalized travel itineraries with realistic budgets and Indian-friendly recommendations
                        </p>
                        <TripForm onSubmit={handleSubmit} isLoading={isLoading} />
                        {error && (
                            <div className="mt-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg max-w-2xl">
                                {error}
                            </div>
                        )}
                    </div>
                ) : (
                    <div className="max-w-4xl mx-auto">
                        <div className="bg-white rounded-xl shadow-lg p-8">
                            <div className="flex justify-between items-center mb-6">
                                <h2 className="text-3xl font-bold text-gray-800">Your AI-Generated Itinerary</h2>
                                <button
                                    onClick={handleReset}
                                    className="px-6 py-2 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-lg transition"
                                >
                                    Create New Trip
                                </button>
                            </div>
                            <div className="prose prose-lg max-w-none">
                                <pre className="whitespace-pre-wrap text-gray-700 font-sans leading-relaxed">
                                    {itinerary}
                                </pre>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}
